body{

    background-color: #000000;
}
h1, h2, p {
        margin: 0;
}
* {
    box-sizing: border-box; /* Ensures padding and borders are included in width/height calculations */
}

.mainContent {
    display: grid;
    grid-template-columns: 1fr; /* One column layout */
    grid-template-rows: auto auto auto auto ; /* Space for five rows */
    row-gap: 10px;
    column-gap: 60px;
    align-items: center; /* Vertically aligns grid items in their cells */
    justify-items: center; /* Horizontally centers grid items in their cells */
    margin-top: 10px;
    max-width: 900px; /* Maximum width of the grid */
    margin: 0 auto; /* Centers the grid horizontally */
    width: 100%; /* Makes the grid take up 100% of the .mainContent width */
    position: relative;
}




.project{
    grid-row: 1/2;
    display: grid;
    align-items: center;
    justify-content: center;
    margin-top: 2vh;


}

.project .iframe{
    width: 900px !important;
    height: 520px;

    display: block;
    object-fit: cover;
}

.title{
    grid-row:2/3;
}

.projectTitle{
    text-align: center;
    font-size: 16px;
    font-family: "Azeret-Mono";
    color: #d4c7c1;

}



.creditsContainer{
    grid-row: 4/5;
    max-width: 900px;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: start; 
    text-align: left;
    line-height: 4vh;
    margin-bottom: 2%;
  
    }


.creditsTitle{
    text-align: center;
    font-size: 12px;
    font-family: "Azeret-Mono";

}

    .creditsList{
        display: flex;
        flex-direction: column;
    }

    .creditItem{
        font-weight: bold; /* makes the title bold */
        margin-right: 10px; /* space between title and name */

    }
    .creditTitle {

        color: #d4c7c1;
        font-size: 12px;
        text-align: left;
        font-family: "Azeret-Mono" ;
 
    }
      
    .creditName {
 
    color: #d4c7c1;
    text-align: left; /* Creates a nice gutter between titles and names */
    font-family: "Azeret-Mono" ;
    font-size: 12px;
    font-weight: 400;
    text-align: left;


    
    }
    

   .creditName  h2{
        display: inline-block;
        font-family: "Azeret-Mono" ;
        font-weight: bold;
    }
    
    

    .rightArrow {
        position: fixed; /* Change from absolute to fixed to make it visible on all page scrolls */
        top: 50%;
        right: 9%;
        transform: translateY(-50%);
        font-size: 1.5rem; /* Increase the size for better visibility */
        color: #d4c7c1; /* Ensure the color contrasts well with the background */
        cursor: pointer;
        z-index: 1000; /* Make sure it's above most other elements */
    }
    
    .leftArrow{
        position: fixed; /* Change from absolute to fixed to make it visible on all page scrolls */
        top: 50%;
        left: 9%;
        transform: translateY(-50%);
        font-size: 1.5rem; /* Increase the size for better visibility */
        color: #d4c7c1; /* Ensure the color contrasts well with the background */
        cursor: pointer;
        z-index: 1000; /* Make sure it's above most other elements */
    }
    


.photo-grid {

    max-width: 900px !important; /* or the width of your content area */
    margin: auto;
  }
  
  .photo-row {
    display: flex;
    justify-content: center;
    margin-bottom: 10px; /* Space between rows */
  }
  
  .photo {
    width: 33.333%; /* Divide by three for three in a row */
    object-fit: cover; /* This will ensure the images cover the area without stretching */
    padding: 5px; /* This creates a small gap between images */
  }
  

  .photoSection{
    display: grid;
    grid-row:4/5;
    grid-template-columns: auto auto auto;
    justify-content: center;
    align-items: center;
    row-gap: 15px;
    column-gap: 15px;
  
  }

  .photoSection img{
    width: 355px;
    height: 237px;
  }


  @media only screen and (max-width: 768px) {

    
    body {
        background-image: url("../../../images/bgPhone.webp") !important;
        background-repeat: no-repeat;
        background-attachment: fixed;
        background-size: cover;
    }


    .mainContent {
        grid-template-columns: 1fr; /* Stacks everything in one column */
        max-width: 100%; /* Use the full width of the screen */
        margin-top: 20px;
        row-gap: 20px;
        column-gap: 0px;
    }

    .project img, .photoSection img {
        width: 100%; /* Make images responsive */
        height: auto; /* Maintain aspect ratio */
        max-width: none; /* Override any max-width set previously */
    }


    .project .iframe{
        width: 350px !important;
        height: 200px;
        display: block;
        object-fit: cover;
    }

 
  

   .projectTitle{
    text-align: center;
    font-size: 10px;
    font-family: "Azeret-Mono";
    color: #d4c7c1;
    margin-bottom: -210px; /* Reduce bottom margin to pull credits closer */

}
   

    .project, .creditsContainer{
        margin-left: 0; /* Remove any left margin */
