    }


    .photoSection {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        row-gap: 30px;
        width: 100%; /* Full width to utilize space but respects max-width of images */
        max-width: 350px !important; /* Maximum width of the container */
        margin: 0 auto; /* Centering the container */
    }

    .photoSection img{
 
        height: auto;
        max-width: 350px !important;
      }
    .photoSection img{
        width: 350px !important;
        height: 278px !important;
      }

      .leftArrow, .rightArrow {
        position: absolute; /* Positions the arrows absolutely relative to the nearest positioned ancestor (mainContent) */
        top: -12.5px; /* Places tces them near the top of mainContent */
        transform: translateY(0); /* Neutralizes any previous transformations */
        z-index: 100; /* Keeps them above other content */
        font-size: 0.8rem;
    }

    .leftArrow{
        left: 15px;
    }

    .rightArrow{
        right: 15px;
    }
    
}



